@import "index.scss";

.wishes {
  width: 100%;
  background-color: #F6FCFF;
  padding: 30px 20px 0;
  text-align: center;
  &--title {
    color: #33424E;
    font-size: 2.8rem;
    font-family: $nunito;
    letter-spacing: 4px;
  }
  &--icon {
    margin: 20px 0;
    color: #BDB7B1;
    font-size: 2rem !important;
  }
  &--content {
    font-family: 'Raleway', sans-serif;
    color: #BDB7B1;
    letter-spacing: 1.5px;
    line-height: 3.2rem;
    font-size: 1.8rem;
  }
  &--button {
    background-color: rgba(51,66,78,1);
    font-family: 'Raleway', sans-serif;
    padding: 10px 30px;
    font-size: 1.8rem;
    color: white;
    border: none;
    margin: 40px;
    cursor: pointer;
  }
}

.user-input {
  width: 100%;
  padding: 20px 30px 0 30px;
  box-sizing: border-box;
  &__container {
    margin-bottom: 10px;
    text-align: left;
    .user-input__name {
      height: 46px;
      width: 100%;
      font-size: 1.8rem;
      border-radius: 0;
      font-family: 'Raleway', sans-serif;
      -webkit-appearance: none;
      -moz-appearance: none;
      border-width: 0 0 1px 0;
      background-color: transparent;
      color: #0F0F0F;
      border-style: solid;
      border-color: rgba(201, 201, 201, 1);
      padding: 3px;
      margin: 0;
      -webkit-box-flex: 1;
      -webkit-flex: 1;
      flex: 1;
      text-overflow: ellipsis;
    }
    .user-area__name {
      height: 100px;
    }
  }
}

