@import "index.scss";
.gallery {
  text-align: center;
  background-color: #F6FCFF;
  &--title {
    color: #33424E;
    font-size: 2.8rem;
    font-family: $nunito;
    letter-spacing: 4px;
  }
  &--icon {
    margin: 20px 0;
    color: #BDB7B1;
    font-size: 2rem !important;
  }
  //.gallery-row.first-image {
  //  margin-top: 20px;
  //}
  .gallery-row:not(.last-image) {
    display: flex;
    margin-bottom: 5px;
    .gallery__mini-image {
      width: 49.5%;
      max-height: 190px;
      margin-right: 1%;
    }
  }
}
