@import "index.scss";

.wedding {
  width: 100%;
  background-color: #F6FCFF;
  padding: 30px 20px;
  text-align: center;
  box-sizing: border-box;
  &--title {
    color: #33424E;
    font-size: 2.8rem;
    font-family: $nunito;
    letter-spacing: 4px;
  }
  &--icon {
    margin: 20px 0;
    color: #BDB7B1;
    font-size: 2rem !important;
  }
  &--content {
    font-family: 'Raleway', sans-serif;
    color: #BDB7B1;
    letter-spacing: 1.5px;
    line-height: 3.2rem;
  }
  &--button {
    background-color: rgba(51,66,78,1);
    font-family: 'Raleway', sans-serif;
    padding: 10px 30px;
    font-size: 1.8rem;
    color: white;
    border: none;
    margin-top: 20px;
    cursor: pointer;
  }
}

.wedding--content_when, .wedding--content_where {
  margin: 20px 40px;
  .title {
    font-size: 2rem;
    color: #41627A;
    margin-bottom: 20px;
  }
  .content {
    font-size: 1.6rem;
  }
}

$black: #202020;
$white: #fff;
$dark-grey: #808080;
$light-grey: #d9d9d9;
$purple: #B97CFC;
$pink: #F93B69;
$green: #B0E8E2;
$orange: #EB8B6E;
.timeline-container {
  display: flex;
}
.timeline {
  margin: auto;
  max-width: 280px;
  font-family: $nunito;
  ul {
    padding: 1rem 0 0 2rem;
    margin: 0;
    list-style: none;
    position: relative;

    &::before {
      content: ' ';
      height: 100%;
      width: 1px;
      background-color: $light-grey;
      position: absolute;
      top: 0;
      left: 3.4rem;
      z-index: 4;
    }
  }
  li {
    margin-bottom: 20px;
  }

  li div{
    display: inline-block;
    margin: 1rem 0;
    vertical-align: top;
  }

  .bullet {
    width: 2rem;
    height: 2rem;
    box-sizing: border-box;
    border-radius: 50%;
    background: $white;
    z-index: 1;
    margin-right: 1rem;

    &.pink {
      border: 2px solid $pink;
    }

    &.green {
      border: 2px solid $green;
    }

    &.orange {
      border: 2px solid $orange;
    }
  }

  .time {
    width: 20%;
    font-size: 1.4rem;
    padding-top: 0.25rem;
    margin-right: 40px;
  }

  .desc {
    padding-top: 2px;
    width: 50%;
  }

  h3 {
    font-size: 1.5rem;
    font-weight: 400;
    margin: 0;
  }

  h4 {
    margin: 0;
    font-size: 1.2rem;
    font-weight: 400;
    color: $dark-grey;
  }
}
