@import "index.scss";

.story {
  width: 100%;
  background-color: #F6FCFF;
  padding: 30px 20px 30px;
  text-align: center;
  box-sizing: border-box;
  strong {
    font-weight: 700;
    font-size: 2rem;
    font-style: normal !important;
  }
  &--title {
    color: #33424E;
    font-size: 2.8rem;
    font-family: $nunito;
    letter-spacing: 4px;
  }
  &--icon {
    margin: 20px 0;
    color: #BDB7B1;
    font-size: 2rem !important;
  }
  &--content {
    font-family: 'Raleway', sans-serif;
    color: #BDB7B1;
    font-size: 1.5rem !important;
    letter-spacing: 1.5px;
    line-height: 3.2rem;
    .text-italic {
      font-style: italic;
    }
    .reverse {
      color: lightblue;
      transform: scaleX(-1);
      -moz-transform: scaleX(-1);
      -webkit-transform: scaleX(-1);
    }
  }
  .left-align {
    text-align: left;
  }
}
